import { Field } from "formik";
import React, { useState } from "react";
import { connect } from "react-redux";
import { Card, CardBody, Col, Form, Row } from "reactstrap";
import {
  DropDown,
  RadioInput,
  ReactStrapFormGroupInput,
  ReactStrapInputWithAddon,
  ReactStrapNumberInput,
} from "../../../components/global-components/Components/Form";
import { FormikHOC } from "../../../components/global-components/Components/ReactStrapFormik";
import initialValues from "./InitialValues.json";
import ValidationSchema from "./ValidationSchema";
import Modal from "../../../components/global-components/Components/Modal/index";
import {
  CheckoutModalBody,
  CheckoutModalHeader,
} from "../../LandlordServices/Components/CheckOutModal/index";
let sizeofmodal = {
  minWidth: "30%",
};
const Referencing = () => {
  const imagealt = "image";
  const [addNewProperty, setaddNewProperty] = useState(false);
  const [showGuarantor, setshowGuarantor] = useState(false);
  const [TenantDetail, setTenantDetail] = useState([
    {
      id: Number(new Date()),
      tenant_sur_name: "",
      tenant_last_name: "",
      tenant_email: "",
      tenant_phone_number: "",
    },
  ]);
  const [isModalVisible, setisModalVisible] = useState(false);

  const toggleModal = () => {
    setisModalVisible(!isModalVisible);
  };

  const addTenantDetail = (e) => {
    e.preventDefault();
    const newTenant = {
      id: Number(new Date()),
      tenant_sur_name: "",
      tenant_last_name: "",
      tenant_email: "",
      tenant_phone_number: "",
    };

    setTenantDetail([...TenantDetail, newTenant]);
  };

  const deleteTenantDetail = (e, index) => {
    e.preventDefault();
    const removetenant = TenantDetail.filter((tenant, i) => i !== index);

    setTenantDetail(removetenant);
  };

  const showGuarantorHandler = (e) => {
    if (e.target.value === "Guarantor") {
      setshowGuarantor(true);
    } else {
      setshowGuarantor(false);
    }
  };

  const showAddNewPropertyHandler = (e) => {
    if (e.target.value === "Add new property details...") {
      setaddNewProperty(true);
    } else {
      setaddNewProperty(false);
    }
  };

  return (
    <Col md="9">
      <div className="">
        <Row>
          <Col md="12">
            <h2 className="sub-title text-dark mt-2 mb-3">
              Tenant Referencing
            </h2>
          </Col>
          <Col md="7">
            <Card>
              <CardBody>
                <h2 className="sub-title">
                  Want to learn more about referencing?
                </h2>
                <p>
                  <a href="/tenant-referencing">
                    Click here to learn about referencing
                  </a>{" "}
                  and why it's important. We have a full page dedicated to our
                  process and the options. Otherwise, continue below if you are
                  looking to get started.
                </p>

                <hr />

                <Form>
                  <RadioInput
                    options={[
                      {
                        label: "Comprehensive",
                        value: "comprehensive",
                      },

                      {
                        label: "Speedy",
                        value: "speedy",
                      },
                    ]}
                    legend="Referencing Type"
                    name="Referencing Type"
                  />

                  <h2 className="sub-title">Property Details</h2>
                  <DropDown
                    label={<b>Select Your Property:</b>}
                    options={[
                      " 1 Rutherford House, 483 Battersea Park Road, London, SW11 4BT (Ref: 903649) ",
                      "Add new property details...",
                    ]}
                    onChange={(e) => showAddNewPropertyHandler(e)}
                  />

                  {addNewProperty ? (
                    <>
                      <h2 className="sub-title my-2">New Property Details:</h2>
                      <Field
                        name="address_line_1"
                        label={<b>Address Line 1</b>}
                        component={ReactStrapFormGroupInput}
                      />
                      <Field
                        name="post_code"
                        label={<b>Post Code</b>}
                        component={ReactStrapFormGroupInput}
                      />
                      <Field
                        name="monthly_rent"
                        position="left"
                        addon="$"
                        label={<b>Monthly Rent ($)</b>}
                        component={ReactStrapInputWithAddon}
                      />
                    </>
                  ) : (
                    <Field
                      name="monthly_rent"
                      position="left"
                      addon="$"
                      label={<b>Monthly Rent ($)</b>}
                      placeholder="14000.00"
                      component={ReactStrapInputWithAddon}
                    />
                  )}

                  <h2 className="sub-title my-3">Tenant / Guarantor Details</h2>
                  <Row>
                    {TenantDetail.map((tenant, index) => (
                      <>
                        <Col md="10" xs="10" sm="10">
                          <Row>
                            <Col md="6" sm="6">
                              <Field
                                name="tenant_sur_name"
                                label={<b>Full SurName </b>}
                                component={ReactStrapFormGroupInput}
                              />
                            </Col>
                            <Col md="6" sm="6">
                              {" "}
                              <Field
                                name="tenant_last_name"
                                label={<b>Full lastName </b>}
                                component={ReactStrapFormGroupInput}
                              />
                            </Col>
                            <Col md="6" sm="6">
                              <Field
                                name="tenant_email"
                                label={<b>Full Email </b>}
                                component={ReactStrapFormGroupInput}
                              />
                            </Col>
                            <Col md="6" sm="6">
                              <Field
                                name="tenant_phone_number"
                                label={<b>Full Phone Number </b>}
                                component={ReactStrapNumberInput}
                              />
                            </Col>
                            <Col md="6" sm="6"></Col>
                          </Row>
                          <RadioInput
                            options={[
                              {
                                label: "Tenant",
                                value: "Tenant",
                              },

                              {
                                label: "Guarantor",
                                value: "Guarantor",
                              },
                            ]}
                            onChange={(e) => showGuarantorHandler(e)}
                            name="tenantType"
                          />
                        </Col>
                        <Col md="2" xs="2" sm="2">
                          <button
                            className="deleteRow btn-sm btn btn-danger text-white"
                            onClick={(e) => deleteTenantDetail(e, index)}
                          >
                            <i className="fas fa-trash-alt"></i>
                          </button>
                        </Col>
                        {showGuarantor ? (
                          <Col md="12">
                            <Field
                              name="tenant_of_guarantor"
                              label={
                                <b>Which tenant(s) is this a guarantor for?</b>
                              }
                              component={ReactStrapFormGroupInput}
                            />
                          </Col>
                        ) : (
                          ""
                        )}
                        <hr className="w-100 mx-2" />
                      </>
                    ))}
                  </Row>

                  <button
                    id="addItem"
                    className="btn btn-default"
                    onClick={(e) => addTenantDetail(e)}
                  >
                    <i className="fa fa-plus"></i> Add Additional Tenant Or
                    Guarantor
                  </button>
                </Form>
              </CardBody>
            </Card>
          </Col>
          <Col md="5">
            <Card>
              <CardBody>
                <h2 className="sub-title">
                  <i className="fas fa-shopping-bag"></i> Order Summary
                </h2>
                <table className="summary-extrainfo-list w-100">
                  <tr>
                    <td>
                      <span className="font-weight-normal">
                        Rental Amount: $14000
                      </span>
                    </td>
                  </tr>
                  <tr></tr>
                </table>
                <table className="summary-list w-100">
                  <tr>
                    <td className="option">2x Reference</td>
                    <td className="value">$40.00</td>
                  </tr>
                </table>

                <hr className="my-3" />
                <div className="total-cont">
                  <span className="option">Total</span>
                  <span className="value">$40.00</span>
                </div>
                <div className=" text-center mt-1">
                  <button
                    className="py-2 btn btn-large btn-success btn-block text-white"
                    onClick={() => toggleModal()}
                  >
                    <i className="fa fa-lock mr-1"></i>
                    <span>Checkout Securely</span>
                  </button>

                  <span className="sm-print">All Prices Include VAT</span>
                  <img
                    alt={imagealt}
                    className="trust-badges w-100"
                    src="//d10hbub4nkludc.cloudfront.net/images/views/gassafety/trust-badges.png"
                  />
                </div>
              </CardBody>
            </Card>

            <Card className="my-4">
              <CardBody>
                <div className="profile text-center">
                  <img
                    alt={imagealt}
                    src="//d36pgh4m67wnlt.cloudfront.net/listings/580446/o_1dech0cmf1d31d7310v01sfp1965b.JPG_t.JPG"
                  />

                  <span className="user-label">Jocelyn</span>
                </div>
                <div className="quote ">
                  <img
                    alt={imagealt}
                    className="text-center star"
                    src="https://d10hbub4nkludc.cloudfront.net/images/LandlordAbout/stars-2.png"
                  />
                  <h3>"The reference service you have is brilliant."</h3>
                  <p>
                    As a landlady DirectRent makes you have some peace of mind
                    that you can check tenants before they sign any contracts -
                    and for only $20 which is very reasonable
                    <br />
                    <small>- DirectRent Landlord</small>
                  </p>
                </div>
              </CardBody>
            </Card>
          </Col>
        </Row>
        <Modal
          isModalVisible={isModalVisible}
          toggle={() => toggleModal()}
          header={<CheckoutModalHeader />}
          style={sizeofmodal}
        >
          <CheckoutModalBody onRequestVerification={() => toggleModal()} />
        </Modal>
      </div>
    </Col>
  );
};

const mapStateToProps = (state, props) => ({
  initialValues: initialValues,
  validationSchema: ValidationSchema,
});
const mapDispatchToProps = (dispatch) => ({});
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(FormikHOC(Referencing));
